import React ,{useState} from 'react'

export default function Textform(props) {
  
  const[text,setText]=useState("Enter text here");
  const handleClick= (e) => {
    e.preventDefault();
    setText(text.toUpperCase());
  }
  const handleChange=(e)=>{
   setText(e.target.value);

  }
  const handleClicklo=(e)=>{
    e.preventDefault();
    setText(text.toLowerCase());
  }
  const clearText=(e)=>{
    e.preventDefault();
    setText("");
  }
  return (
    <>

    <div className="container"  style={{color:props.text}}>
      <form>
  <div className="form-group">
    <h1>{props.heading}</h1>
    <label htmlFor="mybox">Email address</label>
    <textarea className="form-control" onChange={handleChange}  id="mybox" rows="3"  value={text}></textarea>
    <button className="btn btn-primary" onClick={handleClick}>Convert to uppercase</button>
    <button className="btn btn-primary mx-2" onClick={handleClicklo}>Convert to lowercase</button>
    <button className="btn btn-primary " onClick={clearText}>Clear</button>
  </div>
  
  
</form>

    </div>
    <div className="container my-3" style={{color:props.text}}>
      <h1>Your text summary</h1>
      <p>words {text.split(" ").filter(word=>word.length>0).length} characters {text.length} </p>
      <p> time to read {text.split(" ").filter(word=>word.length>0).length*0.008}</p>
      <h2>Preview</h2>
      <p>{text}</p>
    </div>
    </>
  )
}
