import React from "react";
import PropTypes from "prop-types";
// import Link from "react-router-dom";
export default function Navbar(props) {
  return (
    <div className="App">
      <nav className={`navbar navbar-expand-lg navbar-light bg-${props.mode}`}>
        <div className="container-fluid">
          <a className={`navbar-brand text-${props.text}`} href="#">
            {props.title}
          </a>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0 ">
              <li className="nav-item">
                <a
                  className={`nav-link active text-${props.text}`}
                  aria-current="page"
                  href="#"
                >
                  Home
                </a>
              </li>
              {/* <li className="nav-item">
                <a
                  className={`nav-link active text-${props.text}`}
                  aria-current="page"
                  href="/about"
                >
                  {props.about}
                </a>
              </li> */}
            </ul>
            <form className="d-flex" role="search">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                aria-label="Search"
              />
              <button className="btn btn-primary" type="submit">
                Search
              </button>
            </form>
            
            
            <input type="color" className="form-control form-control-color" id="exampleColorInput" value={props.theme} onChange={props.changeTheme} title="Choose your color"/>
           
           
            <div className="form-check form-switch mx-2">
              <input
                className="form-check-input"
                type="checkbox"
                role="switch"
                id="flexSwitchCheckDefault"
                onClick={props.toggleMode}
              />
              <label
                className={`form-check-label text-${props.text}`}
                htmlFor="flexSwitchCheckDefault"
              >
                Change Mode
              </label>
            </div>
          </div>
        </div>
      </nav>
      {/* <div classNameName="turn"><h1>turn of player:{player}</h1></div>
      <div classNameName="grid-container">
  <div classNameName="grid-item" ><button onClick={click}></button></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
  <div classNameName="grid-item"></div>
</div> */}
    </div>
  );
}
Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  about: PropTypes.string.isRequired,
};
Navbar.defaultProps = {
  title: "Put title",
  about: "put about",
};
