function App() {
  const [theme,setTheme]=useState('#FFFFFF');
  const [mode,setMode] = useState('light');
  const[text,setText]=useState('dark');
  const[alert,setAlert]=useState({  
      msg : "",
      type: "",
      visible: false,
    });
  
  const changeAlert=(message,type)=> {
    setAlert({  
      msg : message,
      type: type,
      visible: true,
    });
  }
  const changeTheme=(e)=>{
     setTheme(e.target.value);
     document.body.style.backgroundColor=theme;
  }
  const dismissAlert = () => {
    setAlert({...alert, visible: false});
  }

  const toggleMode=()=> {
    changeAlert('mode change','dismissible')
    if(mode==='dark') {
      document.body.style.backgroundColor="white";
      document.title="Text Utility"
      setMode('light');
      setText('black');
    }
    else{
      document.body.style.backgroundColor="#342158";
      document.title="Text Utility-Dark mode"
      setMode('dark');
      setText('white');
    }
    }
  return (
    <>
    <Navbar title="project" about="about" mode={mode} toggleMode={toggleMode} text={text} theme={theme} changeTheme={changeTheme}/>
    {/* <div className="container"><Textform heading="Enter text to analyse"/></div> */}
    <Alert alert={alert} hideAlert={dismissAlert} />
    <Textform text={text} />
    {/* <BrowserRouter>
    <Routes>
            <Route path="/about" element={<About />}>
            </Route>
            <Route path="/" element={<Textform text={text} />}>
            </Route>
    </Routes>
    </BrowserRouter> */}
    </>
  );
}

export default App;
