import React from 'react'
import useState from 'react'

export default function Alert(props) {
    return (
      props.alert.visible && (
        <div className="alert alert-success alert-dismissible fade show d-flex justify-content-between" role="alert">
          <div>
            <strong>Change!</strong> Your mode has been changed.
          </div>
          <button onClick={props.hideAlert} className="close" data-dismiss="alert" aria-label="close">‚ùå</button>
        </div>
      )
  )
}
